
//   http://localhost:8080/#/app/login
//  code ( code )
//  code 

  import wechatService from '../../service/wechat'
  import userService from '../../service/user'
  import {cookie, locationTool} from '../../service/common'
  import Toast from 'vux/src/components/toast/index.vue'


  export default {
    name: 'app',
    data(){
      return{
        Toast_show:false,
        Toast_text:'',
        is_wx:true, //  false
      }
    },
    async created() {
      var vm=this;
        try {

          // 

          // 

             let code = locationTool.getParams("code");
             let data = await wechatService.getAuthByCode(code);
             console.log("data", data);
             // userService.setUserInfo(data);
             if(data.openId){
              console.log('')
              vm.is_wx=true
              console.log('openId', data.openId)
              vm.$store.commit('openId',data.openId)
              this.$router.push({path:'/app/login'})
             }

             if(data.token){
              console.log('')
              console.log('token', data.token)
              vm.$store.commit('token',data.token)
              vm.is_wx=true
              this.$router.push({path:'/app/main/apply/application/normal'})
             }


        } catch (err) {
          console.log("err", err);
          console.log('\r\n');
          vm.Toast_text='\r\n'
          vm.Toast_show=true
          vm.is_wx=false
        }


    },
    components:{
      Toast
    },
    mounted(){


    },


  }



